import React, {useState, useEffect} from "react";
import axios from "axios"
import BootstrapTable from 'react-bootstrap-table-next';
import paginationFactory, { PaginationProvider } from 'react-bootstrap-table2-paginator';
function TableSort() {

    const [dataTables, setDataTables] = useState([]);

    useEffect(()=>{
        getData()
    },[])
  
    const getData = () => {
        axios("https://jsonplaceholder.typicode.com/comments")
        .then(res => {
            setDataTables(res.data)
        })
    };

    const columns = [
        {
          dataField:"email",
          text:"Email",
          sort:true
        },
        {
          dataField:"postId",
          text:"Product ID",
          sort:true
        },
        {
            dataField:"name",
            text:"Name",
            sort:true
        },
        {
            dataField:"id",
            text:"ID",
            sort:true
        },
        // {
           
        //     text:"SUM",
        //     sort:true
        // },
        // {
        //     //dataField:"id", 
        //     text:"Sum",
        //     sort:true,
        //     formatter: () => {
        //         //   return (
        //         //     dataTables.map(item => {
        //         //         return(
        //         //             item.id
        //         //         )
        //         //     })
        //         //   )

        //     }
        // },
    ]

   
      console.log("Malumotlar: ", dataTables)
   

    return(
        <div>
             <BootstrapTable
               data={dataTables} 
               columns={columns} 
               keyField="id"
               pagination={paginationFactory()}
             />
        </div>
    )
}

export default TableSort;